main {
  min-height: 100vh;
  background: linear-gradient(
    to right top, 
    #65dfc9, 
    #6cdbeb
    );
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

#topBlock,
#bottomBlock {
  min-height: 10vh;
  width: 100vw;
  z-index: 3;
  background: linear-gradient(to right top, #65dfc9, #6cdbeb);
}

.glass {
  background: white;
  min-height: 80vh;
  width: 80%;
  background: linear-gradient(
    to right bottom, 
    rgba(255, 255, 255, 0.7), 
    rgba(255, 255, 255, 0.3)
    );
  border-radius: 2rem; /* or i can use 30px */
    /* backdrop-filter: blur(2rem); */
  display: flex;
}

.textboxes {
  flex: 2;
  overflow: auto;
  /* scroll-snap-type: y mandatory; */
  overflow-y: scroll;
  height: 80vh;
}

.textboxes::-webkit-scrollbar {
  display: none;
}